$bp: (
  mobile: (
    min: 0px,
    max: 480px,
  ),
  tablet: (
    min: 481px,
    max: 820px,
  ),
  smallScreen: (
    min: 821px,
    max: 1440px,
  ),
  largScreen: (
    min: 1440px,
    max: 2440px,
  ),
  extraLarg: (
    min: 2441px,
    max: 3440px,
  ),
);
$color1: #000;
$color2: #fff;
$color3: #ff5e00;
$color4: #4800ff;
$color5: #fff700;

$bgcolor1: #d6fff6;
$bgcolor2: #231651;
$bgcolor3: #4dccbd;
$bgcolor4: #3274ab;
$bgcolor5: #ff8484;

// transpaernt
$tcolor1: #3274abe6;
$tcolor2: #00000049;

@mixin Query($screen) {
  @each $key, $value in $bp {
    @if ($screen == $key) {
      @media (min-width: map-get($value, min)) and (max-width: map-get($value, max)) {
        @content;
      }
    }
    @if ($screen == "extraLarg") {
      @media (min-width: 1201px) {
        @content;
      }
    }
  }
}
@mixin ClearFix {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
}
@mixin RoundedHeader {
  .rounded-header {
    text-align: center;
    padding: 8px 10px;
    border: 3px solid $bgcolor3;
    width: 70%;
    margin: 40px auto;
    color: $color2;
    font-size: 1.5rem;
    font-weight: 300;
    letter-spacing: 1px;
    transition: all ease 0.7s;
    border-radius: 16px;
    &:hover {
      background-color: $bgcolor3;
      color: $color2;
    }
  }
}
@mixin Typography {
  .header {
    font-size: 1.3rem;
    font-weight: 300;
    display: block;
    color: $color2;
    width: auto;
  }
  .span {
    padding-top: 0.4rem;
    float: left;
    font-size: 1rem;
    font-weight: 100;
    width: 100%;
    display: block;
    color: $color2;
  }
  .body {
    padding-top: 0.5rem;
    color: $color2;
    float: left;
    font-size: 1.1rem;
    font-weight: 300;
    width: 100%;
    display: block;
  }
}

@mixin Card {
  .card {
    position: relative;
    margin-top: 6vw;
    height: auto;
    padding-bottom: 20px;
    width: 45%;
    border: 3px solid $bgcolor3;
    background-color: $bgcolor4;
    border-radius: 10px;
    z-index: 5;
    .main-box {
      margin: 2rem;
      padding-top: 3vw;
      height: auto;

      @include Typography;
    }
    .top-box {
      position: absolute;
      top: 0;
      left: 50%;
      border-radius: 10px;
      transform: translate(-50%, -50%);
      padding: 6px;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      height: auto;
      width: 90%;
      border: 3px solid $bgcolor3;
      z-index: 6;
      background-color: inherit;
      h1 {
        padding: 8px auto;
        color: $color2;
        font-size: 1.5rem;
        font-weight: bold;
        width: 100%;
        display: block;
        text-align: center;
      }
      .logo {
        height: auto;
        z-index: 6;
        background-color: inherit;
        img {
          width: 3.3rem;
          height: auto;
          margin: auto;
          display: block;
          margin: 5px;
        }
        .mada {
          border-radius: 0.5rem;
        }
      }
    }
  }
}
