@import "../../styles/mixin.scss";
.link {
  color: $color2;
  font-size: 30px;
  font-weight: bold;
}
.nav-logo{
    display: none;
    font-size: 1rem;
    color: $color2;
    font-weight: 700;
    padding: 16px 16px;
    a{
      text-decoration: none;
    }
}
.active-link {
  color: $bgcolor2;
}
$nav-height: 54px;
$nav-element-pading: 16px 6px;
.navbar {
  height: $nav-height;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 100%;
  height: auto;
  background-color: $bgcolor3;
  position: sticky;
  top: 0;
  z-index: 90;
  #nav-check {
    display: none;
  }
  .nav-btn {
    display: none;
  }
  .mobo-header {
    position: absolute;
    overflow: hidden;
    height: 0px;
    top: 0;
    left: 0;
  }
  .nav-link {
    display: inline;
    margin: auto;
    float: right;
    font-size: 1.1rem;
    @include ClearFix;
    width: auto;
    cursor: pointer;
    a {
      width: auto;
      display: inline-block;
      text-align: center;
      text-decoration: none;
      letter-spacing: 1px;
      transition: all ease 0.5s;
      padding: 16px;
      &:hover {
        color: $bgcolor2;
      }
    }
  }
}

@include Query(mobile) {
  .navbar {
    .nav-logo{
      display: inline-block;
      margin: auto;
    }
    width: 100%;
    .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;

      label {
        display: inline-block;
        width: 46px;
        height: 46px;
        padding: 11px;
        margin: 4px;
        &:hover {
          span {
            border-top: 2px solid $color2;
          }
        }
        span {
          display: block;
          width: 25px;
          height: 10px;
          border-top: 0.125rem solid $color5;
        }
      }
    }
    .nav-link {
      position: absolute;
      display: block;
      width: 100%;
      background-color: $tcolor1;
      height: 0px;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 54px;
      left: 0;
      a {
        padding: 16px 24px;
        display: block;
        width: 100%;
        &:hover {
          color: #fff;
          background-color: $tcolor1;
        }
      }
    }
    #nav-check:checked ~ .nav-btn {
      label {
        background-color: $tcolor2;
        span {
          border-top: 2px solid $color2;
        }
      }
    }
    .mobo-header {
      color: #aba9b4;
      display: block;
      width: 100%;
      height: auto;
      letter-spacing: 1px;
      transition: all ease 1s;
      padding: 16px 24px;
      font-size: medium;
      background-color: $tcolor2;
      height: 100%;
      &:hover {
        color: $color2;
      }
    }
  }
  .navbar > {
    #nav-check:checked ~ .nav-link {
      height: calc(100vh - 50px);
      overflow-y: auto;
    }
    #nav-check:not(checked) ~ .nav-link {
      height: 0px;
    }
  }
}

@include Query(tablet) {
  .navbar {
    width: 100%;
    .nav-logo{
      display: inline-block;
      margin: auto;
    }
    .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;

      label {
        display: inline-block;
        width: 46px;
        height: 46px;
        padding: 11px;
        margin: 4px;
        &:hover {
          span {
            border-top: 2px solid $color2;
          }
        }
        span {
          display: block;
          width: 25px;
          height: 10px;
          border-top: 2px solid #aba9b4;
        }
      }
    }
    .nav-link {
      position: absolute;
      display: block;
      width: 100%;
      background-color: $tcolor1;
      height: 0px;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 54px;
      left: 0;
      a {
        padding: 16px 24px;
        display: block;
        width: 100%;
        &:hover {
          color: $color2;
          background-color: $tcolor1;
        }
      }
    }
    #nav-check:checked ~ .nav-btn {
      label {
        background-color: #3274ab49;
        span {
          border-top: 2px solid #fff;
        }
      }
    }
    .mobo-header {
      color: #aba9b4;
      display: block;
      width: 100%;
      height: auto;
      letter-spacing: 1px;
      transition: all ease 1s;
      padding: 16px 24px;
      font-size: medium;
      border-left: 4px solid #fff;
      background-color: #3274ab49;
      height: 100%;
      &:hover {
        color: #fff;
      }
    }
  }
  .navbar > {
    #nav-check:checked ~ .nav-link {
      height: calc(100vh - 50px);
      overflow-y: auto;
    }
    #nav-check:not(checked) ~ .nav-link {
      height: 0px;
    }
  }
}
